@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&family=Ubuntu:ital,wght@0,300;0,500;0,700;1,300;1,400;1,700&display=swap');

body {
  margin: 0;
  background-color: #181818;
  color: #ffffff;
  font-family: 'Roboto', 'Ubuntu', sans-serif;
}

@tailwind base;
@tailwind components;
@tailwind utilities;


::-webkit-scrollbar {
  background-color: transparent;
  width: 0.5rem;
}

::-webkit-scrollbar-thumb {
  background-color: #c7c7c7;
  border-radius: 1rem;
}

/* Keyframes for modal appearance */
@keyframes modal-appear {
  0% {
      opacity: 0;
      transform: translateY(-50px);
  }
  100% {
      opacity: 1;
      transform: translateY(0);
  }
}

/* Apply animation to modal */
.modal-fade {
  animation: modal-appear 0.3s ease-out forwards;
}
